/**
 * localStorage工具函数 - 用于管理历史记录、用户偏好设置和主题选择
 */

// 常量定义
const HISTORY_KEY = 'genreshift_history';
const PREFERENCES_KEY = 'genreshift_preferences';
const THEME_KEY = 'genreshift_theme';

// 默认值
const DEFAULT_PREFERENCES = {
  defaultOutputStyle: 'news',
  defaultLength: 'medium',
  defaultFocus: 'general',
  defaultLanguage: 'zh',
  saveHistory: true,
};

const DEFAULT_THEME = 'light';

/**
 * 检查localStorage是否可用
 * @returns {boolean} localStorage是否可用
 */
function isLocalStorageAvailable() {
  try {
    const test = '__storage_test__';
    localStorage.setItem(test, test);
    localStorage.removeItem(test);
    return true;
  } catch (e) {
    return false;
  }
}

/**
 * 保存历史记录
 * @param {Object} record - 历史记录对象
 * @returns {boolean} 保存是否成功
 */
export function saveHistoryRecord(record) {
  if (!isLocalStorageAvailable()) return false;
  
  try {
    // 确保record具有必要的属性
    if (!record.id || !record.date) {
      record.id = record.id || Date.now().toString();
      record.date = record.date || new Date().toISOString();
    }
    
    const history = getHistoryRecords();
    
    // 检查是否已存在相同ID的记录
    const existingIndex = history.findIndex(item => item.id === record.id);
    if (existingIndex >= 0) {
      // 更新现有记录
      history[existingIndex] = { ...history[existingIndex], ...record };
    } else {
      // 添加新记录
      history.push(record);
    }
    
    localStorage.setItem(HISTORY_KEY, JSON.stringify(history));
    return true;
  } catch (error) {
    console.error('保存历史记录失败:', error);
    return false;
  }
}

/**
 * 获取所有历史记录
 * @returns {Array} 历史记录数组
 */
export function getHistoryRecords() {
  if (!isLocalStorageAvailable()) return [];
  
  try {
    const historyStr = localStorage.getItem(HISTORY_KEY);
    return historyStr ? JSON.parse(historyStr) : [];
  } catch (error) {
    console.error('获取历史记录失败:', error);
    return [];
  }
}

/**
 * 获取单个历史记录
 * @param {string} id - 记录ID
 * @returns {Object|null} 历史记录对象或null
 */
export function getHistoryRecord(id) {
  if (!isLocalStorageAvailable() || !id) return null;
  
  try {
    const history = getHistoryRecords();
    return history.find(item => item.id === id) || null;
  } catch (error) {
    console.error('获取单个历史记录失败:', error);
    return null;
  }
}

/**
 * 删除历史记录
 * @param {string} id - 记录ID
 * @returns {boolean} 删除是否成功
 */
export function deleteHistoryRecord(id) {
  if (!isLocalStorageAvailable() || !id) return false;
  
  try {
    let history = getHistoryRecords();
    history = history.filter(item => item.id !== id);
    localStorage.setItem(HISTORY_KEY, JSON.stringify(history));
    return true;
  } catch (error) {
    console.error('删除历史记录失败:', error);
    return false;
  }
}

/**
 * 删除多个历史记录
 * @param {Array} ids - 记录ID数组
 * @returns {boolean} 删除是否成功
 */
export function deleteHistoryRecords(ids) {
  if (!isLocalStorageAvailable() || !Array.isArray(ids) || ids.length === 0) return false;
  
  try {
    let history = getHistoryRecords();
    history = history.filter(item => !ids.includes(item.id));
    localStorage.setItem(HISTORY_KEY, JSON.stringify(history));
    return true;
  } catch (error) {
    console.error('删除多个历史记录失败:', error);
    return false;
  }
}

/**
 * 清空所有历史记录
 * @returns {boolean} 清空是否成功
 */
export function clearAllHistory() {
  if (!isLocalStorageAvailable()) return false;
  
  try {
    localStorage.setItem(HISTORY_KEY, JSON.stringify([]));
    return true;
  } catch (error) {
    console.error('清空历史记录失败:', error);
    return false;
  }
}

/**
 * 保存用户偏好设置
 * @param {Object} preferences - 用户偏好设置对象
 * @returns {boolean} 保存是否成功
 */
export function saveUserPreferences(preferences) {
  if (!isLocalStorageAvailable()) return false;
  
  try {
    // 合并现有偏好设置
    const currentPreferences = getUserPreferences();
    const updatedPreferences = { ...currentPreferences, ...preferences };
    localStorage.setItem(PREFERENCES_KEY, JSON.stringify(updatedPreferences));
    return true;
  } catch (error) {
    console.error('保存用户偏好设置失败:', error);
    return false;
  }
}

/**
 * 获取用户偏好设置
 * @returns {Object} 用户偏好设置对象
 */
export function getUserPreferences() {
  if (!isLocalStorageAvailable()) return DEFAULT_PREFERENCES;
  
  try {
    const preferencesStr = localStorage.getItem(PREFERENCES_KEY);
    return preferencesStr 
      ? { ...DEFAULT_PREFERENCES, ...JSON.parse(preferencesStr) } 
      : DEFAULT_PREFERENCES;
  } catch (error) {
    console.error('获取用户偏好设置失败:', error);
    return DEFAULT_PREFERENCES;
  }
}

/**
 * 保存主题偏好
 * @param {string} theme - 主题名称 ('light' 或 'dark')
 * @returns {boolean} 保存是否成功
 */
export function saveThemePreference(theme) {
  if (!isLocalStorageAvailable()) return false;
  
  try {
    // 确保theme是有效值
    const validTheme = theme === 'dark' ? 'dark' : 'light';
    localStorage.setItem(THEME_KEY, validTheme);
    
    // 同步更新HTML元素类名以立即应用主题
    if (typeof document !== 'undefined') {
      if (validTheme === 'dark') {
        document.documentElement.classList.add('dark');
      } else {
        document.documentElement.classList.remove('dark');
      }
    }
    
    return true;
  } catch (error) {
    console.error('保存主题偏好失败:', error);
    return false;
  }
}

/**
 * 获取主题偏好
 * @returns {string} 主题名称 ('light' 或 'dark')
 */
export function getThemePreference() {
  if (!isLocalStorageAvailable()) return DEFAULT_THEME;
  
  try {
    const theme = localStorage.getItem(THEME_KEY);
    return theme || DEFAULT_THEME;
  } catch (error) {
    console.error('获取主题偏好失败:', error);
    return DEFAULT_THEME;
  }
}

/**
 * 根据历史记录计算API使用统计
 * @returns {Object} API使用统计信息
 */
export function getApiUsageStats() {
  if (!isLocalStorageAvailable()) {
    return {
      totalConverts: 0,
      monthlyConverts: 0,
      quota: {
        limit: 100,
        used: 0,
        resetDate: getNextMonthDate(),
      }
    };
  }
  
  try {
    const history = getHistoryRecords();
    const now = new Date();
    const startOfMonth = new Date(now.getFullYear(), now.getMonth(), 1);
    
    // 计算本月转换次数
    const monthlyConverts = history.filter(record => {
      const recordDate = new Date(record.date);
      return recordDate >= startOfMonth && recordDate <= now;
    }).length;
    
    return {
      totalConverts: history.length,
      monthlyConverts,
      quota: {
        limit: 100, // 假设免费版每月限制100次转换
        used: monthlyConverts,
        resetDate: getNextMonthDate(),
      }
    };
  } catch (error) {
    console.error('计算API使用统计失败:', error);
    return {
      totalConverts: 0,
      monthlyConverts: 0,
      quota: {
        limit: 100,
        used: 0,
        resetDate: getNextMonthDate(),
      }
    };
  }
}

/**
 * 获取下个月的日期字符串
 * @returns {string} 下个月的日期字符串，格式为YYYY-MM-DD
 */
function getNextMonthDate() {
  const now = new Date();
  const nextMonth = new Date(now.getFullYear(), now.getMonth() + 1, 1);
  
  const year = nextMonth.getFullYear();
  const month = (nextMonth.getMonth() + 1).toString().padStart(2, '0');
  const day = nextMonth.getDate().toString().padStart(2, '0');
  
  return `${year}-${month}-${day}`;
}

/**
 * 检查是否接近配额限制
 * @returns {boolean} 是否接近配额限制
 */
export function isNearQuotaLimit() {
  const { quota } = getApiUsageStats();
  // 如果已使用超过配额的85%，则返回true
  return quota.used > (quota.limit * 0.85);
}

/**
 * 初始化主题
 * 根据localStorage中存储的主题偏好来设置文档的class
 */
export function initializeTheme() {
  if (typeof document !== 'undefined') {
    const theme = getThemePreference();
    if (theme === 'dark') {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  }
} 